@import url(https://fonts.googleapis.com/css2?family=Texturina:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic&display=swap);
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}
* {
	margin: 0;
	overflow-x: hidden;
	padding: 0;
}


.about {
	height: 100vh;
	width: 100vw;
}
.about__Sections {
	display: flex;
	width: 100vw;
	height: 100vh;
}
.about__left {
	width: 50%;
	height: 100%;
	display: flex;
	justify-content: center;
	flex-direction: column;
	background-color: #222222;
}
.about__Right {
	width: 50%;
	background-color: #f8f8f8;
	height: 100%;
}
.textContainer {
	display: flex;
	flex-direction: column;
	width: 80%;
	height: 60%;
	font-family: "Poppins", sans-serif;
	margin: 0 auto;
	justify-content: space-around;
}

.about__intro {
	color: #6357ff;
	letter-spacing: 3px;
	font-weight: 400;
	font-size: 18px;
	text-transform: uppercase;
}
.about__title {
	width: 100%;
	font-size: 64px;
	font-weight: 400;
	color: #6357ff;
}
.about__description {
	width: 80%;
	word-spacing: 1.5px;
	font-size: 18px;
	line-break: 1.5px;
	color: #f8f8f8;
}

.floatingImg {
	position: absolute;
	right: 0;
	width: 50%;
	top: 115vh;
	right: 8%;
	transform: scaleX(-1);
	box-shadow: 10px 6px 10px;
}
@media screen and (max-width: 1200px) {
	.about__left {
		width: 100%;
	}
	.floatingImg {
		width: 100vw;
		height: 100vh;
		object-fit: cover;
		object-position: center;
		top: 100%;
		right: 0;
		opacity: 0.2;
	}
	.textContainer {
		max-height: 100%;
		height: 100vh;
		width: 80vw;
		justify-content: center;
	}
	.about__title {
		font-size: 50px;
		height: 20%;
		max-height: 40%;
	}
	.about__description {
		font-size: 14px;
		max-height: 70%;
	}
	.about__Right {
		width: 0%;
	}
}

.contact {
	width: 100vw;
	height: 100vh;
}
.contact__sections {
	display: flex;
	width: 100%;
	background-color: #f8f8f8;
	height: 100%;
}

.contact__left {
	width: 60%;
	float: left;
	display: flex;
	height: 90%;
	flex-direction: column;
	justify-content: space-around;
}
.header__Container {
	width: 80%;
	height: 30%;
	margin: 20px auto;
}
.title {
	color: #6357ff;
	text-transform: uppercase;
	font-weight: 400;
	font-size: 16px;
	letter-spacing: 2px;
	opacity: 1;
}
.header {
	font-size: 60px;
	font-weight: 400;
	letter-spacing: 1.2px;
	color: #222222;
	text-transform: capitalize;
}
.inputFieldsContainer {
	display: flex;
	flex-direction: column;
	width: 80%;
	margin: 0 auto;
	height: 70%;
	justify-content: space-between;
}
input,
textarea {
	font-family: "Poppins", sans-serif;
	letter-spacing: 2px;
	background-color: transparent;
	outline: 0;
	height: 30px;
	width: 70%;
	border: none;
	border-bottom: 1px solid gray;
}
input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
	font-size: 14px;
}
input:-ms-input-placeholder, textarea:-ms-input-placeholder {
	font-size: 14px;
}
input::placeholder,
textarea::placeholder {
	font-size: 14px;
}
input:focus,
textarea:focus {
	border-bottom: 1px solid black;
	color: #222222;
}
.btn {
	width: 40%;

	height: 60px;
	font-weight: 600;
	font-family: "Poppins", sans-serif;
	letter-spacing: 2px;
	border-radius: 4px;
	background-color: #6357ff;
	font-size: 14px;
	outline: none;
	cursor: pointer;
	border: 2px solid transparent;
	color: #f8f8f8;
	box-shadow: 0px 0px 10px #6357ff;
	transition: all 0.3s ease-in-out;
}
.btn:hover {
	background-color: transparent;
	border: 2px solid #6357ff;
	color: #6357ff;
}
.contact__right {
	background-color: #6357ff;
	width: 40%;
	height: 100vh;
	justify-content: space-around;
	display: flex;
	flex-direction: column;
}
.contactDetails {
	width: 100%;
	text-align: right;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	float: right;
	height: 60%;
	grid-gap: 2rem;
	gap: 2rem;
	transform: translate(60%, 3%);
}
.contactHeader {
	width: 80%;
	height: 10%;
	margin: 0 auto;
	text-align: center;
	font-family: "Poppins", sans-serif;
	font-size: 20px;
	font-weight: 400;
	justify-content: center;
	display: flex;
	align-items: center;
	letter-spacing: 1.2px;
	color: #f8f8f8;
}
.contactDetails > div {
	font-family: "Poppins", sans-serif;
	height: 100px;
	width: 150px;
	display: flex;
	flex-direction: column;
	color: #f8f8f8;

	justify-content: space-around;
}
.titleContact,
.descriptionContact {
	font-weight: 400;
}
.titleContact {
	width: 60%;
	display: flex;
	justify-content: space-around;
	align-items: center;
	text-align: center;
}
.descriptionContact {
	text-align: left;
}
.maps {
	position: absolute;
	width: 500px;
	cursor: zoom-in;
	right: 20%;
	top: 500%;
	box-shadow: 0px 0px 10px;
}
.socialMedia {
	display: flex;
	width: 100%;

	height: 10%;
	justify-content: center;
	margin: 0 auto;
	align-items: center;
	grid-gap: 2rem;
	gap: 2rem;
}
.socialMedia__link {
	color: #f8f8f8;
	font-size: 30px;
}
@media screen and (max-width: 1000px) {
	.contact__sections {
		display: flex;
		width: 100vw;
		height: 100vh;
		flex-direction: column;
		grid-gap: 10px;
		gap: 10px;
	}
	.header {
		font-size: 50px;
	}
	.maps {
		display: none;
	}
	.contact__left,
	.contact__right {
		width: 100vw;
	}
	.contact__right {
		height: 20vh;
		width: 100vw;
		display: flex;
		justify-content: center;
	}
	.contactDetails {
		transform: translateX(0%);
		width: 95vw;
		height: 100%;
		font-size: 12px;
		display: flex;
		margin: 0 auto;
		flex-direction: row;
		align-items: center;
	}
	.titleContact {
		flex-wrap: wrap;
		margin: 0 auto;
	}
	.descriptionContact {
		text-align: center;
	}
	.contactHeader {
		display: none;
	}
	.socialMedia__link {
		display: none;
	}
}

.homePage {
	width: 100vw;
	height: 100vh;
	background: #6357ff;
}
.halfBg {
	float: right;
	height: 100%;
	background-position: center;
	object-fit: cover;
	width: 60%;
	opacity: 0.6;
}
.homePage__navigation {
	display: flex;
	width: 98vw;
	height: 8vh;
	position: absolute;

	top: 0;
	right: 0%;

	align-items: center;
	justify-content: space-between;
}
.logo {
	font-family: "Texturina", serif;
	font-size: 24px;
	color: #f8f8f8;
	display: flex;
	flex-direction: column;
	justify-content: center;
	text-align: left;
	letter-spacing: 2px;
}
.logo::after {
	content: "EDGE";
	font-size: 12px;
	letter-spacing: 1.1px;
	margin-top: -10px;
}
.socialMedia__container {
	width: 10%;
	height: 100%;

	background-color: #6357ff;
	display: flex;
	align-items: center;
	justify-content: space-evenly;
}
.socialMedia__container > a {
	color: #f8f8f8;
	font-size: 20px;
	transition: all 0.3s;
}
.socialMedia__container > a:hover {
	color: black;
}
.homePage__main {
	height: 100%;
	align-items: center;
	display: flex;
	width: 40%;
	justify-content: center;
}
.main__Contents {
	display: flex;
	flex-direction: column;
	width: 80%;
	color: #f8f8f8;
	text-align: left;
	font-family: "Poppins", sans-serif;
	height: 40%;
	justify-content: space-between;
}
.content__intro {
	font-size: 14px;
	text-transform: uppercase;
	letter-spacing: 3px;
}
.content__title {
	width: 120%;
	font-size: 64px;
	font-weight: 500;
}
.content__description {
	font-size: 20px;
	word-spacing: 1px;
	line-break: 15px;
}
.getStarted {
	width: 40%;
	height: 60px;
	font-weight: 600;
	font-family: "Poppins", sans-serif;
	letter-spacing: 2px;
	border-radius: 4px;
	background-color: #f8f8f8;
	font-size: 14px;
	outline: none;
	cursor: pointer;
	border: 2px solid transparent;
	color: #6357ff;
	transition: all 0.3s ease-in-out;
}

.getStarted:hover {
	background-color: transparent;
	border: 2px solid #f8f8f8;
	color: #f8f8f8;
}
@media screen and (max-width: 1000px) {
	.halfBg {
		width: 100%;
		z-index: 1;
		pointer-events: none;
		opacity: 0.1;
		position: absolute;
	}

	.socialMedia__container {
		flex: 0.3 1;
	}
	.homePage__main {
		width: 100%;
		height: 100%;
	}
	.main__Contents {
		max-height: 100%;
		height: 70%;
		justify-content: center;
		grid-gap: 1rem;
		gap: 1rem;
	}
	.content__intro {
		max-width: 100%;
		height: 10%;
	}
	.content__title {
		max-width: 100%;
		max-height: 60%;
		font-size: 50px;
	}
	.content__description {
		max-width: 100%;
		max-height: 30%;
		font-size: 16px;
	}
}
@media screen and (max-width: 500px) {
	.socialMedia__container {
		display: none;
	}
}

.portfolio {
	margin: 5vh 0;
	height: 80vh;
	width: 100vw;
	font-family: "Poppins";
}
.portfolio__wrapper {
	width: 90vw;
	margin: 0 auto;
	display: flex;
	height: 100%;
	flex-direction: column;
	justify-content: space-around;
}
.portfolio__headerContainer {
	display: flex;
	flex-direction: column;
	justify-content: center;
}
.portfolio__title {
	color: #6357ff;
	text-transform: uppercase;
	font-weight: 400;
	font-size: 24px;
	opacity: 0.8;
	text-align: center;
}
.portfolio__header {
	font-size: 60px;
	font-weight: 400;
	text-align: center;
	letter-spacing: 1.2px;
	color: #222222;
}
.projects__Container {
	display: grid;
	width: 100%;
	grid-template-columns: repeat(4, 1fr);
	place-items: center;
}

.project {
	width: 250px;
	height: 350px;
	overflow: hidden;
	box-shadow: 2px 2px 10px;
}
.project__img {
	background-position: center;
	background-size: cover;
	height: 100%;
	width: 100%;
	transition: all 0.3s;
	-webkit-filter: grayscale();
	        filter: grayscale();
}
.project:hover > .project__img {
	-webkit-filter: none;
	        filter: none;
	transform: scale(1.07);
}
@media screen and (max-width: 1200px) {
	.portfolio {
		height: 100%;
	}

	.projects__Container {
		display: flex;
		flex-direction: column;
		justify-content: center;
		grid-gap: 2rem;
		gap: 2rem;
	}
}

.services {
	width: 100vw;
	height: 100vh;
}
.services__Section {
	display: flex;
	width: 100vw;
	height: 100vh;
}
.services__Left {
	width: 60%;
	height: 100vh;
	display: flex;
	flex-direction: column;
	background-color: #f8f8f8;
}
.topText {
	display: flex;
	flex-direction: column;
	width: 80%;
	height: 20%;
	justify-content: center;
	margin: 0 auto;
}
.service__intro {
	color: #6357ff;
	letter-spacing: 3px;
	font-weight: 400;
	font-size: 18px;
	text-transform: uppercase;
}
.service__title {
	width: 100%;
	font-size: 64px;
	font-weight: 400;
	color: #222222;
}

.servicesContainer {
	height: 80%;
	width: 80%;
	display: grid;
	margin: 0 50px;
	place-items: center;
	grid-column-gap: 3rem;
	-webkit-column-gap: 3rem;
	        column-gap: 3rem;
	grid-template-columns: repeat(2, minmax(300px, 1fr));
}
.servicesCard {
	display: flex;
	flex-direction: column;
	justify-content: center;
	max-width: 300px;
	grid-gap: 5px;
	gap: 5px;
	width: 300px;
	height: 200px;
	padding: 20px;
	box-shadow: inset 0px 0px 5px #222222;
	color: #222222;
	border-radius: 4px;
	background-color: white;
}
.card__Icon {
	font-size: 34px;
	color: #6357ff;
}
.card__Title {
	font-weight: 400;
	font-size: 24px;
	width: 100%;
	display: flex;
	grid-gap: 10px;
	gap: 10px;
	align-items: center;
}
.card__Title::before {
	width: 20%;
	background-color: #6357ff;
	height: 4px;
	content: "";
}
.card__description {
	font-size: 16px;
	width: 90%;
	text-align: justify;
	line-height: 25px;
	font-family: "Montserrat", sans-serif;
}
.about__floatingImg {
	position: absolute;
	top: 205%;
	right: 13%;
	height: 90%;
	box-shadow: 0px 0px 10px;
	border-radius: 10px;
}
.services__Right {
	width: 40%;
	background-color: royalblue;
	height: 100vh;
}
@media screen and (max-width: 1200px) {
	.services__Left {
		width: 100%;
	}
	.services__Right {
		width: 0%;
	}
	.service__title {
		font-size: 45px;
	}
	.servicesContainer {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		grid-gap: 1rem;
		gap: 1rem;

		margin: 0 auto;
	}

	.about__floatingImg {
		display: none;
	}
	.card__Icon {
		font-size: 30px;
	}
	.card__Title {
		font-size: 18px;
	}
	
}

.team {
	width: 100vw;
	height: 80vh;
	margin: 5vh auto;
	font-family: "Poppins", sans-serif;
}

.team__wrapper {
	width: 90vw;
	margin: 0 auto;
	display: flex;
	height: 100%;
	flex-direction: column;
	justify-content: space-around;
}
.team__headerContainer {
	display: flex;
	justify-content: center;
	flex-direction: column;
	height: 30%;
	text-align: center;
}
.team__title {
	color: #6357ff;
	text-transform: uppercase;
	font-weight: 400;
	font-size: 24px;
	opacity: 0.8;
	text-align: center;
}
.team__header {
	font-size: 60px;
	font-weight: 400;
	text-align: center;
	letter-spacing: 1.2px;
	color: #222222;
	text-transform: capitalize;
}
.team__Container {
	display: grid;
	width: 100%;
	grid-template-columns: repeat(4, 1fr);
	place-items: center;
}
.teamCard {
	width: 250px;

	overflow: hidden;
	height: 350px;
	display: flex;
	flex-direction: column;
	cursor: pointer;
}

.team__img {
	width: 100%;
	object-fit: cover;
	object-position: top center;
}
.infoCard {
	background-color: #6357ff;
	overflow: hidden;
	display: flex;
	height: 30%;
	padding: 0 10px;
	align-items: center;
	color: #f8f8f8;
	justify-content: space-between;
}
.titleJob {
	display: flex;
	flex-direction: column;
	width: 70%;
	justify-content: center;
	grid-gap: 4px;
	gap: 4px;
}
.name {
	font-weight: 400;
}
.descriptionJob {
	font-weight: 400;
}
.socialLinks {
	display: flex;
	align-items: center;
	width: 30%;
	justify-content: space-between;
}
@media screen and (max-width: 1000px) {
	.team {
		height: 100%;
	}

	.team__Container {
		display: flex;
		flex-direction: column;
		grid-gap: 1rem;
		gap: 1rem;
	}
}

